import React, { Component} from 'react';
import MobileMenu from "./MobileMenu";
import {sendAmplitudeData} from "../../../../utils/Amplitude";
let classNames = require('classnames');

export default class MobileHeader extends Component {

    constructor(props) {
        super(props);

        this.isActive = false;
    }

    componentDidMount() {
        let link = this;
        let element = document.getElementsByClassName("header_mobile__center")[0];

        element.addEventListener('click', function (event) {
            event.preventDefault();

            let action = link.props.isActive? "closed" : "opened";
            link.isActive = !link.isActive;
            link.forceUpdate();

            sendAmplitudeData("MobileHeader: " + action + " header");
        })
    }

    render() {
        let className = classNames('header_mobile__upper', {
            'header_mobile__upper__active': this.isActive === true,
        });

        return (
            <div className={className}>
                <div className={"header_mobile"}>
                    <div className={"header_mobile__center"}>
                        <div className={"header_mobile__logo"}>
                            {"Reface"}
                        </div>

                        <div className={"header_mobile__arrow_down"}>
                            {
                                (this.isActive)?
                                    <img src={"images/main/header/expand_up.svg"} />
                                    :
                                    <img src={"images/main/header/expand_down.svg"} />
                            }
                        </div>
                    </div>
                </div>

                <MobileMenu handle={this.props.handle} isActive={this.isActive} />
            </div>
        )
    }
}