import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";

export default class Shirt extends Component {

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        let text = strings.policy__text.split('\n').map((item, i) => {
            return <p key={i}>{item}</p>;
        });

        return (
            <div className="app">
                <Header handle={handleStateUpdate.bind(this)} />

                <div className={"container_up_space"}/>
                <div className={"policy"}>
                    <div className={"policy__title"}>{strings.policy__title}</div>
                    <div className={"policy__subtitle"}>{strings.policy__subtitle}</div>
                    <div className={"policy__text"}>{text}</div>
                </div>

                <div className={"container_down_space"}/>
                <Footer />
            </div>
        );
    }
}