import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";
import Functional from "../main/functional/Functional";
import {SpeedMeasure} from "../../utils/SpeedMeasure";

export class Characteristics extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    static getCharacteristicsArray() {
        return [
            {
                "large": {
                    "size": 0.39336299896240234,
                    "url": "gifs/characteristics/large/characteristics_1.gif"
                },
                "middle": {
                    "size": 0.34044551849365234,
                    "url": "gifs/characteristics/middle/characteristics_1.gif"
                },
                "small": {
                    "size": 0.30769824981689453,
                    "url": "gifs/characteristics/small/characteristics_1.gif"
                }
            },
            {
                "large": {
                    "size": 0.23009872436523438,
                    "url": "gifs/characteristics/large/characteristics_3.gif"
                },
                "middle": {
                    "size": 0.2109527587890625,
                    "url": "gifs/characteristics/middle/characteristics_3.gif"
                },
                "small": {
                    "size": 0.19918346405029297,
                    "url": "gifs/characteristics/small/characteristics_3.gif"
                }
            }
        ]
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        let quality = SpeedMeasure.getResolution([
            Characteristics.getCharacteristicsArray()
        ]);

        console.log("quality: " + quality);

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"container_up_space"} />

                <Functional
                    type={"characteristics"}
                    array={Characteristics.getCharacteristicsArray()}
                    quality={quality}
                    title={strings.characteristics__title}
                    description={strings.characteristics__description} />

                <div className={"container_down_space"} />
                <Footer />
            </div>
        )
    }
}